<template>
  <div>
    <CategoryTable :categoryList="categories" />
  </div>
</template>

<script>
  import CategoryTable from './categories-table';
  export default {
    name: 'Event-Categories-page',
    data() {
      return {};
    },
    components: {
      CategoryTable,
    },
    computed: {
      categories() {
        return this.$store.state.CategoriesStore.categories;
      },
    },
    mounted() {
      this.$store.dispatch('CategoriesStore/loadCategoriesList');
    },
  };
</script>
